.canvas-container {
    background-color: black;
}

canvas {
    opacity: 60%;
    left: 50%;
    position: fixed;
    top: 50%;
    -webkit-transform: translate( -50%, -50%);
    -ms-transform: translate( -50%, -50%);
    transform: translate( -50%, -50%);
    /* filter: blur(5px); */
}